import React from 'react';

import styles from './style.module.scss';

const RadioButton = ({ label, itemId, isChecked, getItem, selectedItem }) => {
  const onChange = () => {
    getItem(selectedItem);
  };
  return (
    <>
      <input
        className={styles.radioButton}
        type="radio"
        id={'radio' + itemId}
        name="radio-group"
        checked={isChecked}
        onChange={onChange}
      />
      <label htmlFor={'radio' + itemId}>{label}</label>
    </>
  );
};

export default RadioButton;
