import { useState, useRef, useEffect } from 'react';
import styles from './styles.module.scss';
import {
  setInvoiceReceive,
  setPaymentStep,
  setPaymentMethod,
  setCardNumber,
  setCardExpiryDate,
  setCardCVC,
  saveCardData,
  onCheckRegionInCheckout,
  onCheckCategoryInCheckout,
  checkAllCategoriesAndRegions,
} from '../../redux/actions';
import {
  removeFromBucketStart,
  removeFromBucketSuccess,
} from '../../redux/actions/bucket';
import PaymentProgressBar from '../../components/molecules/PaymentProgressBar';
import CheckoutProduct from './CheckoutProduct';
import Checkbox from '../../components/atoms/Checkbox';
import PaymentSummary from '../../components/molecules/PaymentSummary';
import PaymentMethod from '../../components/molecules/PaymentMethod';
import SwiftPaymentMethod from '../../components/molecules/PaymentMethod/SwiftPaymentMethod';
import CardPaymentMethod from '../../components/molecules/PaymentMethod/CardPaymentMethod';
import { priceWithCommas } from '../../helpers/index';
import { useHistory } from 'react-router-dom';
import { deleteRegionFromCart } from '../../redux/thunk/checkout';
import { getCheckoutPageData } from '../../redux/thunk/checkout';
import Backdrop from '../../components/molecules/Backdrop';
import GrayCloseIcon from '../../assets/images/ui-icons/grayCloseImg.png';
import Input from '../../components/atoms/MyInput';

import { connect } from 'react-redux';

const Checkout = ({
  appInfo,
  currentPaymentStep,
  productsInCart,
  total_outstanding,
  productsCount,
  invoiceReceive,
  paymentMethods,
  paymentMethodKey,
  creditCardData,
  currency_symbol,
  symbol_placement,
  address_line1,
  address_line2,
  city_town,
  region,
  country,
  //email_id,
  postal_code,
  telephone,
  dispatchSetInvoiceReceive,
  dispatchSetPaymentStep,
  dispatchSetPaymentMethod,
  dispatchSetCardNumber,
  dispatchSetExpiryDate,
  dispatchSetCardCVC,
  dispatchSaveCardData,
  dispatchDeleteRegionFromCart,
  dispatchGetCheckoutPageData,
}) => {
  const history = useHistory();

  const [showChangeAddressPopup, setShowChangeAddressPopup] = useState(false);
  const [addressData, setAddressData] = useState({
    address_line1: '',
    address_line2: '',
    city_town: '',
    region: '',
    postal_code: '',
    country: '',
  });

  useEffect(() => {
    dispatchGetCheckoutPageData();
  }, [dispatchGetCheckoutPageData]);

  useEffect(() => {
    if (address_line1) {
      setAddressData({
        ...addressData,
        address_line1: address_line1 ? address_line1 : '',
        address_line2: address_line2 ? address_line2 : '',
        city_town: city_town ? city_town : '',
        region: region ? region : '',
        country: country ? country : '',
        postal_code: postal_code ? postal_code : '',
      });
    }
  }, [address_line1, address_line2, city_town, region, country, postal_code]);

  const paymentMethodHandler = (selectedItem) => {
    dispatchSetPaymentMethod({ paymentKey: selectedItem });
  };

  const addressRef = useRef(null);

  const showChangeAddressPopupHandler = () => {
    setShowChangeAddressPopup(true);
  };

  const hideChangeAddressPopup = () => {
    setAddressData({
      ...addressData,
      address_line1: address_line1 ? address_line1 : '',
      address_line2: address_line2 ? address_line2 : '',
      city_town: city_town ? city_town : '',
      region: region ? region : '',
      country: country ? country : '',
      postal_code: postal_code ? postal_code : '',
    });
    setShowChangeAddressPopup(false);
  };

  const invoiceReceiveHandler = (e) => {
    dispatchSetInvoiceReceive(e.target.checked);
  };

  const setStepHandler = (stepNumber) => {
    dispatchSetPaymentStep(stepNumber);
  };

  const onCancelTransfer = () => {
    history.push('/become-sponsor');
    //must reset state
  };

  const removeRegionHandler = (communityId, regionId) => {
    dispatchDeleteRegionFromCart(communityId, regionId);
  };

  const isSummaryBtnChecked = () => {
    if (currentPaymentStep === 1) {
      return true;
    }
    if (currentPaymentStep === 2) {
      const checkedStatus = Object.entries(paymentMethods).find(
        ([methodName, { checked }]) => checked
      );
      return !!checkedStatus;
    }
  };

  const setAddressDataHandler = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setAddressData({ ...addressData, [name]: value });
  };

  const organisationAddress = `${address_line1 ? address_line1 + '\n' : ''}${
    address_line2 ? address_line2 + '\n' : ''
  }${postal_code ? postal_code + ' ' : ''}${city_town ? city_town + ', ' : ''}${
    region ? region + ', ' : ''
  }${country ? country + '\n' : ''}${telephone ? 'Tel.:' + telephone : ''}`;

  return (
    <div className={styles.checkout}>
      <div
        className={`${styles.checkout__progress} ${styles.checkout__processBarWrap}`}
      >
        <PaymentProgressBar
          step={currentPaymentStep}
          textPoints={['Review', 'Choose Payment Method', 'Pay']}
        />
      </div>
      <div className={styles.checkout__details}>
        <div className={styles.checkout__invoice}>
          {currentPaymentStep === 1 && (
            <div className={styles.checkout__leftBlock}>
              <div
                className={`${styles.checkout__wrap} ${styles.checkout__border}`}
              >
                <p className={styles.checkout__invoice__title}>Invoice to:</p>
                <div className={styles.checkout__invoice__textarea}>
                  <textarea
                    value={organisationAddress}
                    ref={addressRef}
                    disabled={true}
                  ></textarea>
                  <p
                    className={styles.checkout__invoice__changeAddress}
                    onClick={showChangeAddressPopupHandler}
                  >
                    Change the address
                  </p>
                </div>                
                <div className={styles.checkout__invoice__question}>
                  <p className={styles.checkout__invoice__question__title}>
                    Do you need an invoice for an order?
                  </p>
                  <p
                    className={styles.checkout__invoice__question__description}
                  >
                    If you do not provide company data (
                    <span
                      className={
                        styles.checkout__invoice__question__warningText
                      }
                    >
                      such as your legal address
                    </span>
                    ) during the purchase, in some cases you may not receive an
                    invoice for company data.
                  </p>
                  <Checkbox
                    label="I wish to receive an invoice"
                    name="sendInvoice"
                    checked={invoiceReceive}
                    onChangeHandler={invoiceReceiveHandler}
                  ></Checkbox>
                </div>
              </div>

              {Array.isArray(productsInCart) &&
                productsInCart.length > 0 &&
                productsInCart.map((community) => {
                  return (
                    <CheckoutProduct
                      key={
                        community.community_id +
                        '__' +
                        community.community_title
                      }
                      id={community.community_id}
                      title={community.community_title}
                      image={community.basic_community_icon_hover}
                      currency_symbol={currency_symbol}                     
                      regions={community.regions}
                      deleteRegionHandler={removeRegionHandler}
                    />
                  );
                })}
            </div>
          )}

          {currentPaymentStep === 2 && (
            <div
              className={`${styles.checkout__leftBlock} ${styles.checkout__border}`}
            >
              <PaymentMethod
                step={currentPaymentStep}
                setStep={setStepHandler}
                onChangeHandler={paymentMethodHandler}
                paymentMethods={paymentMethods}
                isContinueBtnAble={isSummaryBtnChecked()}
                onCancelTransfer={onCancelTransfer}
                currency_symbol={currency_symbol}
                symbol_placement={symbol_placement}
              ></PaymentMethod>
            </div>
          )}

          {currentPaymentStep === 3 && (
            <>
              {paymentMethodKey === 'SWIFT' && (
                <div className={styles.checkout__border}>
                  <SwiftPaymentMethod
                    step={currentPaymentStep}
                    setStep={setStepHandler}
                    currency={currency_symbol}
                    appInfo={appInfo}
                    totalPrice={priceWithCommas(total_outstanding)}
                    onCancelTransfer={onCancelTransfer}
                  ></SwiftPaymentMethod>
                </div>
              )}
              {paymentMethodKey === 'CREDIT_CARD' && (
                <div
                  className={`${styles.checkout__leftBlock} ${styles.checkout__border}`}
                >
                  <CardPaymentMethod
                    step={currentPaymentStep}
                    setStep={setStepHandler}
                    setCardNumber={dispatchSetCardNumber}
                    setExpiryDate={dispatchSetExpiryDate}
                    setCVCCode={dispatchSetCardCVC}
                    saveCardData={dispatchSaveCardData}
                    totalPrice={priceWithCommas(total_outstanding)}
                    currency={currency_symbol}
                    creditCardData={creditCardData}
                  ></CardPaymentMethod>
                </div>
              )}
            </>
          )}

          {/* summary */}
          {(currentPaymentStep < 3 ||
            (currentPaymentStep === 3 && paymentMethodKey !== 'SWIFT')) && (
            <PaymentSummary
              totalPrice={total_outstanding}
              currency={currency_symbol}                       
              productsCount={productsCount}
              btnText="MAKE FULL PAYMENT"
              position={symbol_placement}
              step={currentPaymentStep}
              clickhandler={setStepHandler}
              checked={isSummaryBtnChecked()}
              onCancelTransfer={onCancelTransfer}
            ></PaymentSummary>
          )}
        </div>
      </div>
      <Backdrop
        show={showChangeAddressPopup}
        clickhandler={hideChangeAddressPopup}
        appearance="dark"
      />
      {showChangeAddressPopup && (
        <div className={styles.checkout__changeAddress}>
          {/* 610 500 */}
          <div className={styles.checkout__changeAddress__header}>
            <div className={styles.checkout__changeAddress__header__title}>
              Update Your Address
            </div>
            <div
              className={styles.checkout__changeAddress__header__close}
              onClick={hideChangeAddressPopup}
            >
              <img src={GrayCloseIcon} alt="close popup icon" />
            </div>
          </div>
          {/* inputs */}
          <div className={styles.checkout__changeAddress__fields}>
            <div className={styles.checkout__changeAddress__fields__wrap}>
              {/* main address */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.address_line1}
                  name="address_line1"
                  onChangeHandler={setAddressDataHandler}
                  label="Address"
                />
              </div>
              {/* address line 2 */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.address_line2}
                  name="address_line2"
                  onChangeHandler={setAddressDataHandler}
                />
              </div>
              {/* city/town */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.city_town}
                  name="city_town"
                  onChangeHandler={setAddressDataHandler}
                  label="City / town"
                />
              </div>
              {/* region */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.region}
                  name="region"
                  onChangeHandler={setAddressDataHandler}
                  label="Region"
                />
              </div>
              {/* postal code */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.postal_code}
                  name="postal_code"
                  onChangeHandler={setAddressDataHandler}
                  label="Postal code"
                />
              </div>
              {/* country */}
              <div className={styles.checkout__changeAddress__fields__input}>
                <Input
                  type="text"
                  value={addressData.country}
                  name="country"
                  onChangeHandler={setAddressDataHandler}
                  label="Country"
                />
              </div>
              {/* buttons */}
              <div className={styles.checkout__changeAddress__buttons}>
                <div
                  className={styles.checkout__changeAddress__buttons__cancel}
                  onClick={hideChangeAddressPopup}
                >
                  <button
                    className={styles.checkout__changeAddress__buttons__btn}
                  >
                    Cancel
                  </button>
                </div>
                <div className={styles.checkout__changeAddress__buttons__add}>
                  <button
                    className={styles.checkout__changeAddress__buttons__btn}
                  >
                    Add
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = (state) => {
  const currency_symbol =
    state.categorygrid.currency_symbol || state.bucket.currency_symbol;
  return {
    productsInCart: state.bucket.inBucket,
    productsCount: state.bucket.productsInBucket,
    appInfo: state.appInfo,
    total_outstanding: state.bucket.total_outstanding,
    currentPaymentStep: state.bucket.paymentDetails.paymentStep,
    paymentMethods: state.bucket.paymentDetails.paymentMethod,
    currency_symbol,
    symbol_placement: state.bucket.symbol_placement,    
    address_line1: state.bucket.paymentDetails.billing_address.address_line1,
    address_line2: state.bucket.paymentDetails.billing_address.address_line2,
    city_town: state.bucket.paymentDetails.billing_address.city_town,
    region: state.bucket.paymentDetails.billing_address.region,
    country: state.bucket.paymentDetails.billing_address.country,
    email_id: state.bucket.paymentDetails.billing_address.email_id,
    postal_code: state.bucket.paymentDetails.billing_address.postal_code,
    telephone: state.bucket.paymentDetails.telephone,
  };
};

const mapDispatchToProps = {
  dispatchSetInvoiceReceive: setInvoiceReceive,
  dispatchRemoveFromBucketStart: removeFromBucketStart,
  dispatchRemoveFromBucketSuccess: removeFromBucketSuccess,
  dispatchOnCheckRegionInCheckout: onCheckRegionInCheckout,
  dispatchOnCheckCategoryInCheckout: onCheckCategoryInCheckout,
  dispatchCheckAllCategoriesAndRegions: checkAllCategoriesAndRegions,
  dispatchSetPaymentStep: setPaymentStep,
  dispatchSetPaymentMethod: setPaymentMethod,
  dispatchSetCardNumber: setCardNumber,
  dispatchSetExpiryDate: setCardExpiryDate,
  dispatchSetCardCVC: setCardCVC,
  dispatchSaveCardData: saveCardData,
  dispatchDeleteRegionFromCart: deleteRegionFromCart,
  dispatchGetCheckoutPageData: getCheckoutPageData,
};

export default connect(mapStateToProps, mapDispatchToProps)(Checkout);
