import styles from './styles.module.scss';
import Button from '../../atoms/Button';
import Radio from '../../atoms/RadioButton';
import PaymentMethod__CardImage from '../../../assets/images/ui-icons/paymentMethod__card.png';
import PaymentMethod__SwiftImageDisabled from '../../../assets/images/ui-icons/paymentMethod__swiftDisabled.png';
import PaymentMethod__CardImageDisabled from '../../../assets/images/ui-icons/paymentMethod__card__disabled.png';
import PaymentMethod__SwiftImage from '../../../assets/images/ui-icons/paymentMethod__swift.png';
import BlueLeftArrow from '../../../assets/images/ui-icons/rightArrowBlue.png';

const CREDIT_CARD = 'CREDIT_CARD';
const SWIFT = 'SWIFT';

const PaymentMethod = ({
  step,
  setStep,
  paymentMethods,
  onChangeHandler,
  isContinueBtnAble,
  onCancelTransfer,
}) => {
  const stepBackHalder = () => {
    setStep(step - 1);
  };

  const continuePaymentHandler = () => {
    setStep(step + 1);
  };

  const creditaCartLabel = (paymentMethods) => {
    return (
      <div className={styles.paymentMethod__chooseMethod__creditCard}>
        <div className={styles.paymentMethod__chooseMethod__creditCard__wrap}>
          <div
            className={
              styles.paymentMethod__chooseMethod__creditCard__information
            }
          >
            <p
              className={`${
                styles.paymentMethod__chooseMethod__creditCard__title
              } ${paymentMethods ? '' : styles.disabledText}`}
            >
              Credit card
            </p>
            <p className={styles.paymentMethod__chooseMethod__creditCard__text}>
              Should arrive by October 24th.
            </p>
            <img
              className={styles.paymentMethod__chooseMethod__creditCard__image}
              src={
                paymentMethods
                  ? PaymentMethod__CardImage
                  : PaymentMethod__CardImageDisabled
              }
              alt="credit card"
            />
          </div>
        </div>
      </div>
    );
  };

  const swiftLabel = (paymentMethods) => {
    return (
      <div className={styles.paymentMethod__chooseMethod__creditCard}>
        <div className={styles.paymentMethod__chooseMethod__creditCard__wrap}>
          <div
            className={
              styles.paymentMethod__chooseMethod__creditCard__information
            }
          >
            <p
              className={`${
                styles.paymentMethod__chooseMethod__creditCard__title
              } ${paymentMethods ? '' : styles.disabledText}`}
            >
              SWIFT transfer
            </p>
            <p className={styles.paymentMethod__chooseMethod__creditCard__text}>
              Send GBP from your bank account outside the UK. Your bank will
              charge you extra fees. Should arrive by October 25th.
            </p>
            <img
              className={styles.paymentMethod__chooseMethod__creditCard__image}
              src={
                paymentMethods
                  ? PaymentMethod__SwiftImage
                  : PaymentMethod__SwiftImageDisabled
              }
              alt="credit card"
            />
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className={styles.paymentMethod}>
      <div className={styles.paymentMethod__wrap}>
        <div className={styles.paymentMethod__header}>
          <p className={styles.paymentMethod__header__title}>
            How will you make your bank transfer?
          </p>
          <p
            className={styles.paymentMethod__header__backBtn}
            onClick={stepBackHalder}
          >
            <img
              className={styles.paymentMethod__header__backImg}
              src={BlueLeftArrow}
              alt="back button"
            />
            Pay another way
          </p>
        </div>
        <div className={styles.paymentMethod__chooseMethod}>
          <div>
            <Radio
              label={creditaCartLabel(paymentMethods[CREDIT_CARD].checked)}
              isChecked={paymentMethods[CREDIT_CARD].checked}
              itemId={CREDIT_CARD}
              selectedItem={CREDIT_CARD}
              getItem={onChangeHandler}
            ></Radio>
          </div>
          <div>
            <Radio
              label={swiftLabel(paymentMethods[SWIFT].checked)}
              isChecked={paymentMethods[SWIFT].checked}
              itemId={SWIFT}
              selectedItem={SWIFT}
              getItem={onChangeHandler}
            ></Radio>
          </div>
        </div>
        <div>
          <div className={styles.paymentMethod__buttons}>
            <Button
              // className={styles.paymentMethod__buttons__cancel}
              clickHandler={onCancelTransfer}
              addClass="grayBG"
            >
              Cancel this transfer
            </Button>
            <Button
              // className={styles.paymentMethod__buttons__continue}
              disabled={!isContinueBtnAble}
              clickHandler={continuePaymentHandler}
              addClass="blueBG"
            >
              Continue to our bank details
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default PaymentMethod;
