const initialState = {
  loader: false,
  regions: regions,
  // categories: categories,
  currency: '€',
};

export const products = (state = initialState, action) => {
  const { type } = action;

  switch (type) {
    case actionTypes.PRODUCT_CHOSEN: {
      return {
        ...state,
        loader: true,
      };
    }

    default:
      return state;
  }
};
