import styles from './styles.module.scss';
import { priceWithCommas } from '../../../helpers';
import { connect } from 'react-redux';
import selectors from '../../../redux/selectors';
import Button from '../../atoms/Button';

const skillVoxPaymentDetails = {
  Name: 'Skillvox BV',
  Email: 'Payments@skillvox.com',
  IBAN: 'GB49BARC20923753650709',
  'Bank code (BIC/SWIFT)': 'BARCGB22',
};

const PaymentSummary = ({
    step,
    totalPrice,
    currency,
    position, 
    productsCount,
    clickhandler,
    onCancelTransfer,
    btnText,
    checked,
 }) => {
  const onClick = () => {
    clickhandler(step + 1);
  };
  return (
    <div className={`${styles.paymentSummary} ${styles.border}`}>
      <div className={styles.paymentSummary__wrap}>
        <div className={styles.paymentSummary__content}>
          <div className={styles.paymentSummary__title}>Summary</div>
          <div className={styles.paymentSummary__totalSponsorship}>
            <p className={styles.paymentSummary__totalSponsorship__title}>
              Total Sponsorship
            </p>
            <p className={styles.paymentSummary__totalSponsorship__price}>
              { position !== 'before' && priceWithCommas(totalPrice) }
                  <span className={styles.paymentSummary__totalSponsorship__currency}>                 
                    {currency}
                  </span>              
              { position === 'before' && priceWithCommas(totalPrice) }               
            </p>
          </div>
          <div
            className={`${styles.paymentSummary__toPay} ${
              step === 2 || step === 3 ? styles.addBottomBorder : ''
            }`}
          >
            <p className={styles.paymentSummary__toPay__title}>To pay</p>           
            <p className={styles.paymentSummary__toPay__price}>
              { position !== 'before' && priceWithCommas(totalPrice) }
                  <span className={styles.paymentSummary__toPay__currency} >
                    {currency}
                  </span>
              { position === 'before' && priceWithCommas(totalPrice) }           
            </p>
          </div>
        </div>
      </div>

      {(step === 2 || step === 3) && (
        <div className={styles.paymentSummary__details}>
          <div className={styles.paymentSummary__wrap}>
            <p className={styles.paymentSummary__details__title}>
              Recipient details
            </p>
            <div className={styles.paymentSummary__details__dataPairs}>
              {skillVoxPaymentDetails &&
                Object.entries(skillVoxPaymentDetails).map((el, idx) => {
                  const [key, value] = el;
                  return (
                    <div
                      className={styles.paymentSummary__details__onePair}
                      key={key + '-' + idx}
                    >
                      <div className={styles.paymentSummary__details__key}>
                        {key}
                      </div>
                      <div className={styles.paymentSummary__details__value}>
                        {value}
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      )}
      <div className={styles.paymentSummary__wrap}>
        <div className={styles.paymentSummary__sponsorBtn}>
          {step === 1 && (
            <Button
              addClass="blueBG"
              uppercase
              disabled={productsCount > 0 ? false : true}
              clickHandler={onClick}
            >
              {btnText}
            </Button>
          )}
          {(step === 2 || step === 3) && (
            <>
              <Button
                addClass="blueBG"
                disabled={!checked}
                clickHandler={onClick}
                uppercase
              >
                {btnText}
              </Button>
              <Button clickHandler={onCancelTransfer} addClass="grayBG">
                Cancel this transfer
              </Button>
              <p className={styles.paymentSummary__differentPayWay}>
                Looking for a different way to pay?
              </p>
            </>
          )}
        </div>
        {step === 1 && (
          <div className={styles.paymentSummary__agreementText}>
            By clicking this button you confirm the purchase. We will ask you to
            confirm the payment in your bank’s mobile application.
          </div>
        )}
      </div>
      <div className={styles.paymentSummary__generateQuote}>
        <Button text="Generate a Quote" addClass="grayBigBG"></Button>
      </div>
    </div>
  );
};

export default connect(selectors, null)(PaymentSummary);
