import actionTypes from '../constants';

export function loadCommunityDataStart() {
  return {
    type: actionTypes.LOAD_SPONSOR_COMMUNITIES_START,
  };
}

export function loadCommunityDataError(payload) {
  return {
    type: actionTypes.LOAD_SPONSOR_COMMUNITIES_ERROR,
    payload,
  };
}

export function loadCommunityDataSuccess(payload) {
  return {
    type: actionTypes.LOAD_SPONSOR_COMMUNITIES_SUCCESS,
    payload,
  };
}

export function toggleAddCommunityToCart(payload) {
  return {
    type: actionTypes.TOGGLE_ADD_COMMUNITY_TO_CART,
    payload,
  };
}

export function setAtiveCommunity(payload) {
  return {
    type: actionTypes.SET_ACTIVE_COMMUNITY,
    payload,
  };
}

export function resetActiveCommunity() {
  return {
    type: actionTypes.RESET_ACTIVE_COMMUNITY,
  };
}
