import actionTypes from '../constants';

const initialState = {
  isAuth: false,
  email: '',
  loader: false,
};

export const auth = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.LOGIN_INPUT_EMAIL: {
      return {
        ...state,
        email: payload.email,
      };
    }

    case actionTypes.LOG_IN_START: {
      return {
        ...state,
        loader: true,
      };
    }

    default:
      return state;
  }
};
