import styles from './styles.module.scss';

const PaymentProgressBar = ({ step, textPoints }) => {
  return (
    <div className={styles.progressBar}>
      <div className={styles.progressBar__points}>
        <div
          className={`${styles.progressBar__points__onePoint} ${
            step > 1 ? styles.stepDone : ''
          }`}
        >
          <div
            className={`${styles.progressBar__points__onePoint__step} ${
              step > 1 ? styles.stepDone : ''
            } ${step === 1 ? styles.currentStep : ''}`}
          ></div>
        </div>
        <div
          className={`${styles.progressBar__points__onePoint}  ${
            step > 2 ? styles.stepDone : ''
          }`}
        >
          <div
            className={`${styles.progressBar__points__onePoint__step} ${
              step > 2 ? styles.stepDone : ''
            } ${step === 2 ? styles.currentStep : ''}`}
          ></div>
        </div>
        <div
          className={`${styles.progressBar__points__onePoint} ${styles.stepDone}`}
        >
          <div
            className={`${styles.progressBar__points__onePoint__step} ${
              step > 3 ? styles.stepDone : ''
            } ${step === 3 ? styles.currentStep : ''}`}
          ></div>
        </div>
      </div>
      <div className={styles.progressBar__titles}>
        {textPoints &&
          textPoints.map((text, idx) => {
            return (
              <div
                key={text + '__' + idx}
                className={styles.progressBar__titles__text}
              >
                {text}
              </div>
            );
          })}
      </div>
    </div>
  );
};
export default PaymentProgressBar;
