import actionTypes from '../constants';

const initialState = {
  loader: false,
  inBucket: [],
  productsInBucket: 0,
  total_paid: 0,
  total_pledged: 0,
  total_outstanding: 0,
  currency_symbol: '',
  preferred_currency: '',
  symbol_placement: '',
  invoiceReceive: false,
  paymentDetails: {
    paymentStep: 1,
    paymentMethod: {
      CREDIT_CARD: {
        methodId: null,
        checked: false,
        cardNumber: '',
        expiryDate: '',
        CVC: '',
        isCardSaved: false,
      },
      SWIFT: {
        methodId: null,
        checked: false,
      },
    },
    billing_address: {
      address_line1: '',
      address_line2: '',
      city_town: '',
      region: '',
      postal_code: '',
      country: '',
      email_id: '',
    },
    campaign_title: '',
    telephone: '',
  },
};

export const bucket = (state = initialState, { type, payload }) => {
  switch (type) {
    case actionTypes.ADD_TO_BUCKET_START:
    case actionTypes.FETCH_CHECKOUT_PAGE_START:
    case actionTypes.REMOVE_FROM_BUCKET_START:
    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_START: {
      return {
        ...state,
        loader: true,
      };
    }

    case actionTypes.UPDATE_BUCKET_PRODUCTS: {
      const {
        products,
        total_paid,
        total_pledged,
        total_outstanding,
        productsCount,
      } = payload;

      return {
        ...state,
        inBucket: products,
        productsInBucket: productsCount,
        total_outstanding: Number(total_outstanding),
        total_paid: Number(total_paid),
        total_pledged: Number(total_pledged),
        loader: false,
      };
    }

    case actionTypes.ADD_TO_BUCKET_SUCCESS: {
      const {
        community_id,
        regionData: { region_id, name, price, payment_status },
      } = payload;

      const communityInBuket = state.inBucket.find(
        (el) => el.community_id === community_id
      );
      if (communityInBuket) {
        const newRegionsInCommunity = [
          ...communityInBuket.regions,
          { name, region_id, price, payment_status, chosen: true },
        ];
        const inBucketWithoutCurrentCommunity = state.inBucket.filter(
          (el) => el.community_id !== community_id
        );
        const total_outstanding = newRegionsInCommunity.reduce((acc, el) => {
          return acc + el.price;
        }, 0);
        return {
          ...state,
          loader: false,
          inBucket: [
            ...inBucketWithoutCurrentCommunity,
            { ...communityInBuket, regions: newRegionsInCommunity },
          ],
          productsInBucket: state.productsInBucket + 1,
          total_outstanding,
        };
      }

      return {
        ...state,
        loader: false,
      };
    }

    case actionTypes.REMOVE_FROM_BUCKET_SUCCESS: {
      const { community_id, regionId } = payload;

      const communityInBuket = state.inBucket.find(
        (el) => el.community_id === community_id
      );
      if (communityInBuket) {
        const newRegionsInCommunity = communityInBuket.regions.filter(
          (el) => el.region_id !== regionId
        );
        const inBucketWithoutCurrentCommunity = state.inBucket.filter(
          (el) => el.community_id !== community_id
        );
        const total_outstanding = newRegionsInCommunity.reduce((acc, el) => {
          return acc + Number(el.price);
        }, 0);
        return {
          ...state,
          loader: false,
          inBucket: [
            ...inBucketWithoutCurrentCommunity,
            { ...communityInBuket, regions: newRegionsInCommunity },
          ],
          productsInBucket: state.productsInBucket - 1,
          total_outstanding,
        };
      }
      return {
        ...state,
        loader: false,
      };
    }

    case actionTypes.FETCH_CHECKOUT_PAGE_SUCCESS: {
      const {
        billing_address,
        campaign_title,
        community,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        telephone,
        total_outstanding,
        total_paid,
        total_pledged,
      } = payload;

      let productsCount = 0;
      community.map((el) =>
        el.regions.map((reg) => reg.chosen === true && productsCount++)
      );

      return {
        ...state,
        loader: false,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address,
          campaign_title,
          telephone,
        },
        inBucket: [...community].sort((a, b) =>
          a.community_title.localeCompare(b.community_title)
        ),
        productsInBucket: productsCount,
      };
    }

    case actionTypes.FETCH_CHECKOUT_PAGE_ERROR: {
      const { error: message, statusCode } = payload;
      return {
        ...state,
        loader: false,
        error: {
          message,
          statusCode,
        },
      };
    }

    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_SUCCESS: {
      const {
        billing_address,
        campaign_title,
        communities,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
      } = payload;

      let productsCount = 0;
      communities.map((el) =>
        el.regions.map((reg) => reg.chosen === true && productsCount++)
      );

      return {
        ...state,
        loader: false,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address,
          campaign_title,
        },
        inBucket: [...communities].sort((a, b) =>
          a.community_title.localeCompare(b.community_title)
        ),
        productsInBucket: productsCount,
      };
    }

    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_ERROR: {
      const { error: message, statusCode } = payload;
      return {
        ...state,
        loader: false,
        error: {
          message,
          statusCode,
        },
      };
    }

    case actionTypes.PAY_FOR_OFFERED_PRODUCTS: {
      const newInBucket = state.inBucket.map((el) => {
        return { ...el, payed: true };
      });
      return {
        ...state,
        loader: false,
        inBucket: newInBucket,
      };
    }

    case actionTypes.LOGOUT_START: {
      return {
        ...state,
        inBucket: {},
        productsInBucket: 0,
        total_outstanding: 0,
      };
    }

    case actionTypes.SET_INVOICE_RECEIVE: {
      return {
        ...state,
        invoiceReceive: payload,
      };
    }

    case actionTypes.SET_PAYMENT_STEP: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentStep: payload,
        },
      };
    }

    case actionTypes.SET_PAYMENT_METHOD: {
      const { paymentKey } = payload;
      const newPaymentMethod = Object.entries(
        state.paymentDetails.paymentMethod
      ).reduce((acc, el) => {
        const [method, paymentData] = el;
        if (method === paymentKey) {
          return { ...acc, [method]: { ...paymentData, checked: true } };
        }
        return { ...acc, [method]: { ...paymentData, checked: false } };
      }, {});
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentMethod: newPaymentMethod,
        },
      };
    }

    case actionTypes.SET_PAYMENT_CARD_NUMBER: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentMethod: {
            ...state.paymentDetails.paymentMethod,
            CREDIT_CARD: {
              ...state.paymentDetails.paymentMethod.CREDIT_CARD,
              cardNumber: payload,
            },
          },
        },
      };
    }

    case actionTypes.SET_PAYMENT_CARD_EXPIRY_DATE: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentMethod: {
            ...state.paymentDetails.paymentMethod,
            CREDIT_CARD: {
              ...state.paymentDetails.paymentMethod.CREDIT_CARD,
              expiryDate: payload,
            },
          },
        },
      };
    }

    case actionTypes.SET_PAYMENT_CARD_CVC: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentMethod: {
            ...state.paymentDetails.paymentMethod,
            CREDIT_CARD: {
              ...state.paymentDetails.paymentMethod.CREDIT_CARD,
              CVC: payload,
            },
          },
        },
      };
    }

    case actionTypes.SAVE_PAYMENT_CARD_DATA: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentMethod: {
            ...state.paymentDetails.paymentMethod,
            CREDIT_CARD: {
              ...state.paymentDetails.paymentMethod.CREDIT_CARD,
              isCardSaved: payload,
            },
          },
        },
      };
    }

    default:
      return state;
  }
};
