import React from 'react';
import styles from './styles.module.scss';
import BlueLeftArrow from '../../../../assets/images/ui-icons/rightArrowBlue.png';
import Button from '../../../atoms/Button';
import creditCardNumberImg from '../../../../assets/images/ui-icons/creditCardNumber__input.png';
import creditCardCVCImg from '../../../../assets/images/ui-icons/creditCardCVC__input.png';
import Checkbox from '../../../atoms/Checkbox';

const CardPaymentMethod = (props) => {
  const {
    step,
    setStep,
    totalPrice,
    currency,
    creditCardData,
    // onCancelTransfer,
    setCardNumber,
    setExpiryDate,
    setCVCCode,
    saveCardData,
  } = props;
  const stepBackHalder = () => {
    setStep(step - 1);
  };

  const cardNumberHandler = (e) => {
    const value = e.target.value.replace(/[^\d]/g, '');
    setCardNumber(value);
  };

  const expiryDateHandler = (e) => {
    const value = e.target.value.replace(/[^\d]/g, '');
    setExpiryDate(value);
  };

  const cvcCodeHandler = (e) => {
    const value = e.target.value.replace(/[^\d]/g, '');
    setCVCCode(value);
  };

  const saveCardDataHandler = (e) => {
    saveCardData(e.target.checked);
  };

  const formatCreditCardNumber = (ccNumber) => {
    if (ccNumber) {
      return ccNumber.match(/.{1,4}/g).join('  ');
    }
    return '';
  };

  const formatCreditCardExpiryDate = (date) => {
    if (date.length >= 3) {
      return date.match(/.{1,2}/g).join('/');
    }
    return date;
  };

  return (
    <div className={styles.cardPayment}>
      <div className={styles.cardPayment__wrap}>
        <div className={styles.cardPayment__header}>
          <div className={styles.cardPayment__header__title}>
            How will you make your bank transfer?
          </div>
          <div className={styles.cardPayment__backBtn} onClick={stepBackHalder}>
            <img
              src={BlueLeftArrow}
              className={styles.cardPayment__backBtn__img}
              alt="right blue arrow"
            />
            <p className={styles.cardPayment__backBtn__text}>Pay another way</p>
          </div>
        </div>

        <div className={styles.cardPayment__cardDetails}>
          <div className={styles.cardPayment__cardDetails__row}>
            <div className={styles.cardPayment__cardDetails__cardNumber}>
              <label htmlFor="cardNumber">Card number</label>
              <div className={styles.cardPayment__cardDetails__inputWrap}>
                <input
                  type="tel"
                  name="cardNumber"
                  id="cardNumber"
                  inputMode="numeric"
                  //   pattern="\d*"
                  autoComplete="cc-number"
                  maxLength="22"
                  placeholder="XXXX XXXX XXXX XXXX"
                  value={formatCreditCardNumber(creditCardData.cardNumber)}
                  onChange={cardNumberHandler}
                />
                <img
                  className={styles.cardPayment__cardDetails__image}
                  src={creditCardNumberImg}
                  alt="card template"
                />
              </div>
            </div>
          </div>
          <div className={styles.cardPayment__cardDetails__row}>
            <div className={styles.cardPayment__cardDetails__expiryDate}>
              <label htmlFor="expiryDate">Expiry date (MM/YY)</label>
              <div className={styles.cardPayment__cardDetails__inputWrap}>
                <input
                  name="expiryDate"
                  id="expiryDate"
                  type="tel"
                  inputMode="numeric"
                  maxLength="5"
                  value={formatCreditCardExpiryDate(creditCardData.expiryDate)}
                  onChange={expiryDateHandler}
                />
              </div>
            </div>
            <div className={styles.cardPayment__cardDetails__cvc}>
              <label htmlFor="cvc">CVV/CVC</label>
              <div className={styles.cardPayment__cardDetails__inputWrap}>
                <input
                  type="text"
                  name="cvc"
                  id="cvc"
                  maxLength="3"
                  value={creditCardData.CVC}
                  onChange={cvcCodeHandler}
                />
                <img
                  className={styles.cardPayment__cardDetails__image}
                  src={creditCardCVCImg}
                  alt="card template"
                />
              </div>
            </div>
          </div>
        </div>

        <div>
          <Checkbox
            label="Save card for future payments"
            name="sendInvoice"
            checked={creditCardData.isCardSaved}
            labelTextColor="grayText"
            onChangeHandler={saveCardDataHandler}
          ></Checkbox>
        </div>

        <div className={styles.cardPayment__button}>
          <Button addClass="blueBG">
            Pay {totalPrice} {currency}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default CardPaymentMethod;
