import styles from './styles.module.scss';
import TrashImg from '../../../assets/images/ui-icons/trash.png';
import { priceWithCommas } from '../../../helpers/index';
import Button from '../../../components/atoms/Button';

const CheckoutProduct = ({
  title,
  id,
  image,
  regions,
  currency_symbol, 
  deleteRegionHandler,
}) => {
  return (
    <div className={styles.community}>
      <div className={styles.community__wrap}>
        {/* header */}
        <div className={styles.community__header}>
          <div className={styles.community__header__image}>
            <img src={'https://voxdigital.by' + image} alt={title + ' icon'} />
          </div>
          <div className={styles.community__header__data}>
            <div className={styles.community__header__data__title}>{title}</div>
            <div className={styles.community__header__data__campaign}>
              Upfront for 2021/22 sponsorship campaign
            </div>
          </div>
          <div className={styles.community__header__paymentButton}>
            <Button
              text="Make a partial payment"
              addClass="whiteBG__blueBorder"
              // clickHandler={onSponsor}
            ></Button>
          </div>
        </div>
        {/* regions */}
        <div className={styles.community__regions}>
          {regions.map((region) => (
            <div
              className={styles.community__regions__single}
              key={region.region_id + '__' + region.name}
            >
              <div className={styles.community__regions__single__title}>
                {region.name}
              </div>
              <div className={styles.community__regions__single__countries}>
                <span
                  className={
                    styles.community__regions__single__countries__count
                  }
                >
                  {region.countriesCount} countries
                </span>{' '}
                <span
                  className={
                    styles.community__regions__single__countries__title
                  }
                >
                  {region.countriesText}
                </span>
              </div>
              <div className={styles.community__regions__single__price}>
                {priceWithCommas(Number(region.price))}
                <span
                  className={styles.community__regions__single__price__currency}
                >
                  {currency_symbol}
                </span>
              </div>
              <div className={styles.community__regions__single__program}>
                <span
                  className={styles.community__regions__single__program__pilot}
                >
                  Pilot sponsorship program:
                </span>{' '}
                <span
                  className={
                    styles.community__regions__single__program__impressions
                  }
                >
                  3 million sponsor impressions
                </span>
              </div>
              {/* remove from cart */}
              <div
                className={styles.community__regions__single__remove}
                onClick={() => deleteRegionHandler(id, region.region_id)} //TODO
              >
                <img src={TrashImg} alt="remove from cart icon" />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default CheckoutProduct;
